@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&family=Roboto:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.banner-wrapper {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.laptop-logo {
  height: 100px;
  width: auto;
}

.banner-header {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  margin-bottom: 0;
  font-size: 25px;
  color: #333333;
}

.banner-subheader {
  font-family: 'Baloo Tammudu 2', cursive;
  margin-top: 0;
  font-size: 18px;
  color: #333333;
}

@media screen and (max-width: 800px) {
  .banner-wrapper {
    height: 100vh;
    padding-top: 40px;
  }
}

a {
  color: grey;
  text-decoration: none;
}

a:hover {
  color: rgba(13, 44, 102, 0.938);
}

.section-wrapper {
  position: static;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  min-height: 45vw;
  padding-bottom: 4%;
}

.section-text {
  position: relative;
  padding: 0 2rem;
  width: 30%;
}

.section-link {
  margin: 0;
}

.section-header {
  font-family: 'Montserrat', sans-serif;
  font-size: 2vw;
  letter-spacing: 0.5px;
  color: rgb(53, 53, 53);
  margin: 1.8em 0;
}

.section-title,
.github-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2vw;
  letter-spacing: 0.5px;
  color: rgb(75, 75, 75);
  margin-bottom: 0;
}

.github-title {
  margin-top: 0;
}

.section-detail {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.1vw;
  line-height: 2.1vw;
  letter-spacing: 0.875px;
}

.tech-stack {
  padding: 0%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  width: 90%;
}

ul {
  list-style-type: none;
}

.tech-item {
  height: 4vw;
  margin-top: 0.5em;
  padding-right: 1em;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.tech-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.section-image {
  width: 58%;
}

.laptop-container {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding-bottom: 13%;
  width: 60%;
}

.laptop-frame {
  position: absolute;
  width: 92%;
}

.laptop-screen {
  width: 71%;
  position: relative;
  padding-top: 10%;
  right: 10.5%;
}

.c-img {
  height: 96%;
  border-radius: 5px;
}

.mobile-image {
  height: 50vw;
}

@media screen and (max-width: 800px) {
  .section-wrapper {
    position: static;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 5vw 0;
    min-height: 100vw;
    width: 100vw;
  }

  .section-text {
    width: 90%;
  }

  .section-header {
    text-align: center;
    font-size: 27px;
  }

  .section-title,
  .github-title {
    font-size: 20px;
    letter-spacing: 0.5px;
  }

  .section-detail {
    font-size: 15px;
    line-height: 20px;
  }

  .tech-stack {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    width: 100%;
    margin-bottom: 2em;
  }

  .laptop-screen {
    width: 71.55%;
    padding-top: 10%;
    right: 10.35%;
  }
}

.tech-logo {
  height: 50px;
}

@media screen and (max-width: 800px) {
  .tech-logo {
    height: 30px;
  }
}

.site-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  height: 10vh;
  position: fixed;
  top: 0;
  background-color: white;
  width: 100%;
  z-index: 1000;
}

.header-logo {
  height: 40px;
  padding-right: 2rem;
}

.github-logo {
  padding-right: 1rem;
}

@media screen and (max-width: 800px) {
  .site-header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    height: 5vh;
    position: fixed;
    top: 0;
    background-color: white;
    width: 100%;
    z-index: 1000;
  }

  .header-logo {
    height: 30px;
    padding-right: 1rem;
  }
}

.footer-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 90vh;
}

.footer-image {
  width: 800px;
}

@media screen and (max-width: 800px) {
  .footer-image {
    width: 500px;
  }
  .footer-wrapper {
    height: 100vh;
  }
}

